<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item label="">
					<el-select v-model="filters.term" placeholder="">
						<el-option label="2016-2017-1" value="shanghai"></el-option>
						<el-option label="2016-2017-2" value="beijing"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getUsers"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="handleAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="users" highlight-current-row v-loading="listLoading" @selection-change="selsChange"  height="360" style="width: 140%;">
			<el-table-column type="selection" width="38" fixed>
			</el-table-column>
			<el-table-column type="index" width="68" fixed>
			</el-table-column>
			<el-table-column prop="name" label="" width="180" sortable>
			</el-table-column>
			<el-table-column prop="tNum" label="" width="140" :formatter="formatSex" sortable>
			</el-table-column>
			<el-table-column prop="approvalUnit" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="aDate" label="" width="140" sortable>
			</el-table-column>
			<el-table-column prop="category" label="" min-width="140" sortable>
			</el-table-column>
			<el-table-column prop="standard" label="" min-width="140" sortable>
			</el-table-column>
			<el-table-column prop="score" label="" min-width="140" sortable>
			</el-table-column>
			<el-table-column label="" width="150" fixed="right">
				<template scope="scope">
					<el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
					<el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>

		<!---->
		<el-col :span="24" class="toolbar">
			<el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
			<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="20" :total="total" style="float:right;">
			</el-pagination>
		</el-col>

		<!---->
		<el-dialog title="" v-model="editFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="150px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tNum">
					<el-input v-model="editForm.tNum" auto-complete="off" placeholder="130079" disabled></el-input>
				</el-form-item>
				<el-form-item label="" prop="approvalUnit">
					<el-input v-model="editForm.approvalUnit" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="cNum">
					<el-input v-model="editForm.cNum" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="aDate">
					<el-date-picker type="date" placeholder="" v-model="aDate"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.category" placeholder="">
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="()" value="()"></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
					</el-select>
				</el-form-item>		
				<el-form-item label="">
					<el-input placeholder="2016-2017-1" v-model="editForm.time" disabled></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="150px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tNum">
					<el-input v-model="editForm.tNum" auto-complete="off" placeholder="130079" disabled></el-input>
				</el-form-item>
				<el-form-item label="" prop="approvalUnit">
					<el-input v-model="editForm.approvalUnit" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="cNum">
					<el-input v-model="editForm.cNum" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="aDate">
					<el-date-picker type="date" placeholder="" v-model="aDate"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.category" placeholder="">
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="." value="."></el-option>						
						<el-option label="()" value="()"></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
						<el-option label="" value=""></el-option>						
					</el-select>
				</el-form-item>		
				<el-form-item label="">
					<el-input placeholder="2016-2017-1" v-model="editForm.time" disabled></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="addFormVisible = false"></el-button>
				<el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>

<script> 
	import util from '../../common/js/util'
	//import NProgress from 'nprogress'
	import { getUserListPage, removeUser, batchRemoveUser, editUser, addUser } from '../../api/api';

	export default {
		data() {
			return {
				filters: {
					term: ''
				},
				users: [],
				total: 0,
				page: 1,
				listLoading: false,
				sels: [],//

				editFormVisible: false,//
				editLoading: false,
				editFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				editForm: {
					id: 0,
					name: '',
					tNum: '',
					approvalUnit: '',
					cNum: '',
					category: ''
				},
				aDate: '',

				addFormVisible: false,//
				addLoading: false,
				addFormRules: {
					name: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				//
				addForm: {
					id: 0,
					name: '',
					tNum: '',
					approvalUnit: '',
					cNum: '',
					category: ''
				},
				aDate: '',

			}
		},
		methods: {
			/* //
			formatSex: function (row, column) {
				return row.sex == 1 ? '' : row.sex == 0 ? '' : '';
			},
			handleCurrentChange(val) {
				this.page = val;
				this.getUsers();
			}, */
			//
			getUsers() {
				let para = {
					page: this.page,
					name: this.filters.term
				};
				this.listLoading = true;
				//NProgress.start();
				getUserListPage(para).then((res) => {
					this.total = res.data.total;
					this.users = res.data.users;
					this.listLoading = false;
					//NProgress.done();
				});
			},
			//
			handleDel: function (index, row) {
				this.$confirm('?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { id: row.id };
					removeUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			},
			//
			handleEdit: function (index, row) {
				this.editFormVisible = true;
				this.editForm = Object.assign({}, row);
			},
			//
			handleAdd: function () {
				this.addFormVisible = true;
				this.addForm = {
					name: '',
					sex: -1,
					age: 0,
					birth: '',
					addr: ''
				};
			},
			//
			editSubmit: function () {
				this.$refs.editForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.editLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.editForm);
							para.pDate = (!para.pDate || para.pDate == '') ? '' : util.formatDate.format(new Date(para.pDate), 'yyyy-MM-dd');

							editUser(para).then((res) => {
								this.editLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['editForm'].resetFields();
								this.editFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			//
			addSubmit: function () {
				this.$refs.addForm.validate((valid) => {
					if (valid) {
						this.$confirm('', '', {}).then(() => {
							this.addLoading = true;
							//NProgress.start();
							let para = Object.assign({}, this.addForm);
							para.pDate = (!para.pDate || para.pDate == '') ? '' : util.formatDate.format(new Date(para.pDate), 'yyyy-MM-dd');
							addUser(para).then((res) => {
								this.addLoading = false;
								//NProgress.done();
								this.$message({
									message: '',
									type: 'success'
								});
								this.$refs['addForm'].resetFields();
								this.addFormVisible = false;
								this.getUsers();
							});
						});
					}
				});
			},
			selsChange: function (sels) {
				this.sels = sels;
			},
			//
			batchRemove: function () {
				var ids = this.sels.map(item => item.id).toString();
				this.$confirm('', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					//NProgress.start();
					let para = { ids: ids };
					batchRemoveUser(para).then((res) => {
						this.listLoading = false;
						//NProgress.done();
						this.$message({
							message: '',
							type: 'success'
						});
						this.getUsers();
					});
				}).catch(() => {

				});
			}
		},
		mounted() {
			this.getUsers();
		}
	}

</script>

<style scoped>	
	.el-table{
		font-size:12px;
	}
</style>


