						<el-option label="SCI" value="SCI"></el-option>
						<el-option label="EI" value="EI"></el-option>
						<el-option label="EI()" value="EI()"></el-option>
						<el-option label="ISTP" value="ISTP"></el-option>
						<el-option label="A+" value="A+"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="" value=""></el-option>
						<el-option label="()-" value="()-"></el-option>
						<el-option label="()-" value="()-"></el-option>
						<el-option label="-" value="-"></el-option>
						<el-option label="-" value="-"></el-option>
						<el-option label="A+SCI" value="A+SCI"></el-option>
						<el-option label="EISCI" value="EISCI"></el-option>
						<el-option label="EISCI" value="EISCI"></el-option>
						<el-option label="AEI" value="AEI"></el-option>
						<el-option label="JCRSCI-E" value="JCRSCI-E"></el-option>
						<el-option label="AEI/SCI" value="AEI/SCI"></el-option>
						<el-option label="A-" value="A-"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="B" value="B"></el-option>
						<el-option label="C" value="C"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="B" value="B"></el-option>
						<el-option label="C" value="C"></el-option>
						<el-option label="A+" value="A+"></el-option>
						<el-option label="" value=""></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="accession">
					<el-input v-model="editForm.accession" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="periodVolume">
					<el-input v-model="editForm.periodVolume" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="start">
					<el-input v-model="editForm.start" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="end">
					<el-input v-model="editForm.end" auto-complete="off"></el-input>
				</el-form-item>				
				<el-form-item label="">
					<el-input placeholder="2016-2017-1" v-model="editForm.time" disabled></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click.native="editFormVisible = false"></el-button>
				<el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
			</div>
		</el-dialog>

		<!---->
		<el-dialog title="" v-model="addFormVisible" :close-on-click-modal="false">
			<el-form :model="editForm" label-width="100px" :rules="editFormRules" ref="editForm">
				<el-form-item label="" prop="name">
					<el-input v-model="editForm.name" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="tNum">
					<el-input v-model="editForm.tNum" auto-complete="off" placeholder="130079" disabled></el-input>
				</el-form-item>
				<el-form-item label="" prop="periodical">
					<el-input v-model="editForm.periodical" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="publish">
					<el-date-picker type="date" placeholder="" v-model="publish"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.rank" placeholder="">
						<el-option label="1" value="1"></el-option>
						<el-option label="2" value="2"></el-option>
						<el-option label="3" value="3"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="">
					<el-select v-model="editForm.category" placeholder="">
						<el-option label="SCI" value="SCI"></el-option>
						<el-option label="EI" value="EI"></el-option>
						<el-option label="EI()" value="EI()"></el-option>
						<el-option label="ISTP" value="ISTP"></el-option>
						<el-option label="A+" value="A+"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="" value=""></el-option>
						<el-option label="()-" value="()-"></el-option>
						<el-option label="()-" value="()-"></el-option>
						<el-option label="-" value="-"></el-option>
						<el-option label="-" value="-"></el-option>
						<el-option label="A+SCI" value="A+SCI"></el-option>
						<el-option label="EISCI" value="EISCI"></el-option>
						<el-option label="EISCI" value="EISCI"></el-option>
						<el-option label="AEI" value="AEI"></el-option>
						<el-option label="JCRSCI-E" value="JCRSCI-E"></el-option>
						<el-option label="AEI/SCI" value="AEI/SCI"></el-option>
						<el-option label="A-" value="A-"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="B" value="B"></el-option>
						<el-option label="C" value="C"></el-option>
						<el-option label="A" value="A"></el-option>
						<el-option label="B" value="B"></el-option>
						<el-option label="C" value="C"></el-option>
						<el-option label="A+" value="A+"></el-option>
						<el-option label="" value=""></el-option>
					</el-select>
				</el-form-item>
				<el-form-item label="" prop="accession">
					<el-input v-model="editForm.accession" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="periodVolume">
					<el-input v-model="editForm.periodVolume" auto-complete="off"></el-input>
				</el-form-item>
